<script>
export default {
  props: {
    color: { type: String, default: "#ffffff" },
    roundel: { type: String, default: "france" },
  },
  data() {
    return {
      target: "base",
      colors: {
        base: "#0c8928",
        radar: "#ffffff",
        pattern: "#ffffff",
        cockpit: "#1a1a1a",
        cockpitBorder: "#ffffff",
        dash: "#ff5555",
      }
    };
  },
  mounted() {
    console.log(`emit initColor(${this.colors[this.target]})`);
    this.initWithTargetColor();
  },
  methods: {
    setTarget(trg) {
      console.log(`setTarget(${trg})`);
      this.target = trg;
      this.initWithTargetColor();
    },
    initWithTargetColor() {
      console.log(`emit initColor(${this.colors[this.target]})`);
      this.$emit("initColor", this.colors[this.target]);
    },
    assetPath(roundel) {
      var images = require.context('@/assets/roundels', false, /\.svg$/)
      return images('./' + roundel + ".svg")
    }
  },
  watch: {
    color: {
      handler(value) {
        console.log(`watch change taget color(${this.target}, ${value})`);
        var colors = { ...this.colors };
        colors[this.target] = value;
        this.colors = colors;
      },
    },
  },
};
</script>