<template>
  <div class="wrapper">
    <div>
      <Mirage2000 id = "paper" :color="color" :roundel="roundel" @initColor="initColor" />
      <youtube :video-id="videoId"/>
    </div>
    <div>
      <v-color-picker
        v-model="color"
        dot-size="25"
        swatches-max-height="100"
        show-swatches
      ></v-color-picker>
      
      <div class="roundels">
        <div
          v-for="roundel in roundels"
          :key="roundel"
          style="width: 64px;"
        >
          <v-img class="roundel" :src="assetPath(roundel)" :width="rounelRatio(roundel) * 32" :height="32" v-on:click="setRoundel(roundel)" />
        </div>
      </div>
      
      <v-btn v-on:click="printOut('paper', 'html')">Print</v-btn>
    </div>
  </div>
</template>

<script>
import Mirage2000 from "./Mirage2000.vue";
import printJS from 'print-js'

import { getIdFromURL } from 'vue-youtube-embed'

export default {
  components: { Mirage2000 },
  data() {
    return {
      color: "#ffffff",
      roundel: "france",
      roundels: [ "france", "saudi", "canada", "swiss", "us", "greece", "german", "soviet", "korea", "japan"],
      roundelsRatios: 
        {us: 204.0/108, korea: 1160 / 481.4},
      videoId: getIdFromURL("https://youtu.be/2ZnIgt4x4Yg")
    };
  },
  methods: {
    initColor(color) {
      console.log(`initColor(${color})`);
      this.color = color;
    },
    assetPath(roundel) {
      var images = require.context('@/assets/roundels', false, /\.svg$/)
      return images('./' + roundel + ".svg")
    },
    setRoundel(roundel) {
      this.roundel = roundel
    },
    printOut(id, type) {
      printJS(id, type)
    },
    rounelRatio(id) {
      var ratio = this.roundelsRatios[id]
      if (ratio === undefined) {
        return 1.0
      }
      return ratio
    }
  },
};
</script>

<style scoped>
.wrapper {
  display: flex;
}
.roundel {
  margin: 5px auto;
}
.roundels {
  display: flex;
  flex-flow: wrap;
  margin: 5px;
  overflow:scroll; 
  width: 300px;
  height: 100px;
}
</style>
